* {
  box-sizing: border-box;
  margin: 0;
}
:root {
  --mainGreen: #376f02;
  --headings: #387002;
  --font_color: #f6f06d;
}
.App {
  text-align: center;
}
.App-header {
  background-color: var(--mainGreen);
  height: 70px;
  display: flex;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: #f6f06d;
  padding: 2% 12%;
  align-items: center;
}
.header-text {
  display: flex;
}
.logo-container {
  display: flex;
  align-items: center;
}
.logo-container img {
  height: 70px;
  width: 70px;
  padding: 8px;
}
.menu-bar {
  position: relative;
  display: inline-block;
}
.bar {
  cursor: pointer;
}
.menu-content {
  display: none;
  min-width: 222px;
  background-color: var(--mainGreen);
  list-style: none;
  position: absolute;
  right: 0;
  box-shadow: 0px 8px 16px 0px rgb(0 0 0 / 20%);
  padding: 1rem 2rem;
  flex-direction: column;
  z-index: 1;
}
.menu-content a {
  cursor: pointer;
  padding: 0.5rem 0;
}
.container {
  margin: 1rem 12%;
}
.animation1 {
  animation: slide 0.5s ease-in;
}
.animation2 {
  animation: slide 0.5s ease-in;
  animation-delay: 1s;
}
.animation3 {
  animation: slide 0.5s ease-in;
  animation-delay: 2s;
}
@keyframes slide {
  0% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0px);
  }
}
.start {
  text-align: left;
  color: #67a042;
  text-transform: uppercase;
  font-size: 3.3vw;
}
.start span {
  font-weight: 600;
}
.start div {
  display: inline-block;
  margin-right: 1vw;
}
main {
  display: flex;
}
.info {
  font-size: 2.5vw;
  text-align: left;
}
.info img {
  width: 200px;
  border-radius: 5px;
  margin: 1rem 0;
  display: block;
}
.text1 {
  color: #2b9348;
}
.text2 {
  font-weight: 500;
  color: #656565;
}
.banners {
  display: flex;
  justify-content: space-evenly;
  transform: translateY(-1.5vw);
  margin: 0 0 0 5%;
}
.banners img {
  width: 13rem;
  object-fit: cover;
}
.banner-right {
  margin-left: 3%;
  transform: translateY(calc(-1vw - 10px));
}
.banner-left {
  display: flex;
  flex-direction: column;
}
.banner1 {
  height: 59.2%;
  border-top-left-radius: 20px;
  margin-bottom: 4%;
}
.banner2 {
  border-bottom-left-radius: 20px;
  height: 36%;
}
.banner3 {
  margin-bottom: 3%;
  border-top-right-radius: 20px;
}
.banner4 {
  border-bottom-right-radius: 20px;
}
.about {
  margin: 2rem 0;
}
.about h3 {
  text-align: center;
  margin-bottom: 1rem;
}
.about > p {
  margin-bottom: 1rem;
}
.about h3,
.about p {
  color: rgb(82, 81, 81);
}
.about img {
  float: left;
  height: auto;
  width: 255px;
  margin-right: 2%;
}
.about div {
  text-align: left;
}
#fbGoals {
  margin: 4rem 0;
}
#fbGoals h2 {
  font-weight: 300;
}
#stats h2 {
  font-weight: 300;
  margin-bottom: 1rem;
}
.Rating {
  border-radius: 50%;
  background-image: linear-gradient(#397102, #73d814);
  width: 15%;
  padding-top: 15%;
  margin: 1rem;
  position: relative;
  text-align: left;
  box-shadow: 2px 2px 12px grey;
}
.Rating p {
  color: #f6f06d;
  position: absolute;
  top: 22%;
  left: 24%;
  font-size: 1.7vw;
  font-weight: 500;
}
.Rating span {
  position: absolute;
  font-size: 2.3vw;
}
.goals {
  display: flex;
  margin: 1.7rem 0;
  justify-content: space-evenly;
}
.Goal {
  height: 186px;
  width: 19%;
  border-radius: 10px;
  box-shadow: 2px 2px 20px grey;
  padding: 6px;
  max-width: 150px;
}
.Goal p {
  color: #387002;
  font-size: 2.1vw;
  margin-bottom: 1rem;
  font-weight: 600;
}
.Goal svg {
  color: #a7a7a7;
}
.contain {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}
.images {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1rem;
}
#crops {
  margin: 2rem 0;
}
#crops h2 {
  margin: 1rem;
  font-weight: 300;
  color: var(--headings);
}
.crop {
  height: 270px;
  width: 100%;
  border-radius: 20px;
  position: relative;
  background-color: #6c9e3c;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  border-radius: 20px;
  opacity: 0.5;
  z-index: 5;
}
.crop-content {
  position: absolute;
  bottom: 0;
  margin: 2vw;
  width: 90%;
  z-index: 20;
  display: flex;
  justify-content: space-between;
}
.organic {
  color: #f6f06d;
}
.crop-name {
  color: white;
  font-size: 1.4rem;
  font-weight: 600;
  text-transform: capitalize;
}
.buy-btn {
  border-radius: 20px;
  background-color: #5eac14;
  color: #f6f06d;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  margin: auto 0;
  font-weight: 700;
}
.buy-btn:hover {
  background-color: #99e055f7;
}
.downloader {
  position: absolute;
  width: 60%;
  height: 82%;
  font-size: 2rem;
  text-overflow: ellipsis;
  margin: 2vw;
  color: var(--font_color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.downloader img {
  width: 80%;
  border-radius: 5px;
  margin: 0.5rem 0;
}
#team h2 {
  font-weight: 300;
  color: var(--headings);
  margin-bottom: 1rem;
}
.name h4 {
  text-transform: capitalize;
  color: var(--headings);
}
.candidates {
  display: flex;
  justify-content: space-evenly;
}
.candidates .Rating {
  width: 73%;
  padding-top: 73%;
}
.candidates img {
  object-fit: contain;
}
#farmers {
  margin: 4rem 0;
}
#farmers h2 {
  font-weight: 300;
  margin: 2rem;
  color: var(--headings);
}
#farmers p {
  font-weight: 500;
}
#farmers img {
  width: 100%;
  margin-top: 2rem;
}
#partners h2 {
  font-weight: 300;
  color: var(--headings);
  margin-bottom: 1rem;
}
.partner-logo {
  display: flex;
  justify-content: space-evenly;
  margin: 1rem;
}
.partner-logo img {
  width: 14%;
}
.partner-logo img:hover {
  box-shadow: 0px 0px 6px -1px grey;
  cursor: pointer;
}
#achievements {
  margin: 1rem;
}
#achievements > h2 {
  color: var(--headings);
  font-weight: 300;
  margin-bottom: 1rem;
}
.achievement-block {
  width: 100%;
  display: grid;
  grid-template-columns: auto auto;
}
.achievement {
  display: flex;
  width: 100%;
  height: 240px;
}
.achievement .description {
  flex-basis: 40%;
  background-color: #6c9e3c;
  position: relative;
  z-index: 10;
}
.description {
  color: white;
  text-transform: capitalize;
}
.description .date {
  color: var(--font_color);
}
.day {
  font-size: 4.4vw !important;
  font-weight: 800;
}
.date {
  display: flex;
  margin-left: 0.5rem;
}
.date div {
  margin-top: 1vw;
}
.date p {
  font-size: 1.6vw;
}
.description .title {
  font-size: 2vw;
  font-weight: 700;
  margin: 1vw 0;
}
.description .representative {
  margin: 1vw 0;
}
.description::after {
  content: "";
  width: 0;
  height: 0;
  border-top: 60px solid transparent;
  border-bottom: 60px solid transparent;
  right: -20px;
  top: 40%;
  z-index: -1;
  border-left: 60px solid #6c9e3c;
  position: absolute;
}
.achievement img {
  width: 22vw;
}
.testimonials {
  display: flex;
  color: white;
  position: relative;
}
.testimonials .overlay {
  border-radius: unset !important;
  z-index: 1;
}
.individual {
  display: flex;
  height: 300px;
  flex-basis: 130%;
  padding: 7vw 2vw;
  color: white;
  z-index: 20;
  flex-direction: column;
  justify-content: center;
}
.individual p {
  font-size: 2vw;
}
.individual-image {
  width: 11.5vw;
  padding-top: 60%;
  border-radius: 50%;
  margin: 50% auto;
  margin-bottom: inherit;
}
.testimonials-info {
  text-align: left;
  z-index: 20;
  font-size: 2.2vw;
  padding: 5vw 2vw;
}
.testimonials-info h3 {
  margin-bottom: 4vw;
}
.bottom-text {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 1rem;
}
.bottom-text p {
  font-size: 1.4rem;
  font-weight: 700;
  padding: 0.5rem;
}
.bottom-text img {
  width: 200px;
  border-radius: 10px;
}
.footer {
  background-color: var(--mainGreen);
  display: flex;
  justify-content: space-evenly;
  height: auto;
  color: #f6f06d;
}
.footer > div {
  width: 33%;
}
.footer h3 {
  margin: 2% 3%;
}
.social-icons {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 4rem;
}
.social-icons svg {
  padding: 0.5rem;
}
.Contacts div {
  display: flex;
  justify-content: left;
  align-items: center;
  margin: 4px;
}
.Contacts div p {
  margin-left: 4%;
  text-align: left;
  font-size: 0.8rem;
}
.numbers {
  flex-direction: column;
}
@media screen and (max-width: 640px) {
  .menu-bar {
    position: unset;
  }
  .menu-content {
    bottom: -55px;
    left: 0;
    top: 70px;
    justify-content: space-evenly;
  }
  .start {
    font-size: 1.8rem;
    margin-bottom: 1.2rem;
    text-align: center;
  }
  main {
    flex-direction: column-reverse;
  }
  .info {
    text-align: center;
    align-content: center;
    font-size: 1.5rem;
  }
  .info img {
    margin: 1rem auto;
  }
  .banners {
    margin: 1.4rem 5% 0.5rem 5%;
  }
  .banners img {
    width: 9.1rem;
  }
  .goals {
    flex-direction: column;
  }
  .Goal {
    display: flex;
    flex-direction: row-reverse;
    width: 100vw;
    justify-content: left;
    height: auto;
    align-items: center;
    margin: 2%;
    padding: 1rem;
    max-width: 100%;
  }
  .Goal p {
    font-size: 2rem;
    margin-left: 1rem;
  }
  .crop {
    height: 200px;
  }
  .crop-name {
    font-size: 3vw;
  }
  .buy-btn {
    padding: 0.5rem;
    font-size: 2.7vw;
  }
  .downloader {
    font-size: 1.3rem;
    margin: 3vw;
  }
  .achievement-block {
    grid-template-columns: auto;
  }
  .achievement .description {
    flex-basis: unset;
  }
  .achievement img {
    width: 38vw;
  }
  .day {
    font-size: 11.5vw !important;
  }
  .date p {
    font-size: 4.7vw;
  }
  .description .title {
    font-size: 4vw;
  }
  .description .representative {
    margin: 3vw 0;
  }
  .individual {
    height: 200px;
  }
  .testimonials-info {
    font-size: 3.2vw;
  }
  .bottom-text {
    flex-direction: column;
  }
  .footer {
    flex-direction: column;
  }
  .footer > div {
    width: 100%;
    text-align: left;
    margin: 0.2rem 2.7rem;
  }
  .social-icons {
    padding: 0 0.5rem;
    justify-content: left;
  }
  .social-icons svg {
    margin-left: 0.4rem;
    padding: 0;
  }
}
